$gray: #808080;

.header {
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid $gray;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

  &_nav {
    margin-left: 25px;
    display: flex;
    column-gap: 36px;

    &_link {
      text-decoration: none;
      font-size: 24px;
      font-weight: 700;
      color: #333;
      transition-duration: 0.3s;

      &:hover {
        color: #c14f3b;
        transition-duration: 0.4s;
      }
    }
  }

}